<template>
  <div class="container">
    <div
      class="d-flex"
      v-for="business in approvedBusinesses"
      :key="business.id"
    >
      <div>{{business.name}}</div>
      <b-button
        variant="link"
        :to="{name: 'Business', params: {id: business.id}}"
        class="ml-auto"
      >
        visit
      </b-button>
    </div>

  </div>
</template>

<script>
  import firebase from 'firebase';
  import _ from 'lodash';
  export default {
    name: "ApprovedBusinesses",

    data(){
      return {
        approvedBusinessIds: {},
        businesses: {},
      }
    },

    computed:{
      approvedBusinesses(){
        let approved = [];
        console.log(this.businesses, this.approvedBusinessIds)
        if (_.isEmpty(this.businesses) || _.isEmpty(this.approvedBusinessIds)){
          return approved;
        }
        for (const id in this.approvedBusinessIds){
          if (this.approvedBusinessIds[id]){
            let business = this.businesses[id];
            if (business){
              business.id = id;
              approved.push(business);
            }
          }
        }
        return approved;
      }
    },

    mounted(){
      firebase
        .database()
        .ref('/approved')
        .on('value', snapshot => {
          console.log(snapshot.val());
          this.approvedBusinessIds = snapshot.val();
        })
      firebase
        .database()
        .ref('/businesses')
        .on('value', snapshot => {
          this.businesses = snapshot.val();
        })
    }
  }
</script>

