<template>
  <div>
    <div
      class="d-flex"
      v-for="business in pendingBusinesses"
      :key="business.id"
    >
      <div>{{business.name}}</div>
      <b-button
        variant="link"
        :to="{name: 'Business', params: {id: business.id}}"
        class="ml-auto"
      >
        visit
      </b-button>
    </div>
  </div>
</template>

<script>
  import firebase from "firebase";

  export default {
    name: "PendingApproval",
    computed: {
      pendingBusinesses(){
        let pending = [];
        for (const id in this.businesses){
          if (!this.approvedDisapprovedIds.includes(id)){
            let bus = this.businesses[id];
            bus.id = id;
            pending.push(bus);
          }
        }
        return pending;
      }
    },

    data(){
      return {
        approvedDisapprovedIds: {},
        businesses: {},
      }
    },

    mounted(){
      firebase
        .database()
        .ref('/approved')
        .on('value', snapshot => {
          this.approvedDisapprovedIds = Object.keys(snapshot.val())
        })
      firebase
        .database()
        .ref('/businesses')
        .on('value', snapshot => {
          this.businesses = snapshot.val();
        })
    }
  }
</script>

