<template>
  <b-navbar toggleable="lg" type="dark" variant="dark">
    <b-navbar-brand to="/">Home</b-navbar-brand>
    <b-navbar-nav>
      <b-nav-item :to="{name: 'Types'}">Business Types</b-nav-item>
    </b-navbar-nav>
    <b-button variant="info" @click="logout" class="ml-auto" v-if="loggedIn">Logout</b-button>
  </b-navbar>
</template>

<script>
  import firebase from 'firebase';

  export default {
    name: "Nav",
    methods: {
      logout(){
        firebase.auth().signOut();
      }
    },
    data(){
      return {
        loggedIn: false
      }
    },

    mounted(){
      firebase.auth().onAuthStateChanged(user => {
        this.loggedIn = user;
      })
    }
  }
</script>

