<template>
  <div class="container">
    <div
      class="d-flex"
      v-for="business in disapprovedBusinesses"
      :key="business.id"
    >
      <div>{{business.name}}</div>
      <b-button
        variant="link"
        :to="{name: 'Business', params: {id: business.id}}"
        class="ml-auto"
      >
        visit
      </b-button>
    </div>

  </div>
</template>

<script>
  import firebase from 'firebase';
  export default {
    name: "DisapprovedBusinesses",

    data(){
      return {
        disapprovedBusinessIds: {},
        businesses: {},
      }
    },

    computed:{
      disapprovedBusinesses(){
        let disapproved = [];
        console.log(this.businesses, this.disapprovedBusinessIds)
        if (_.isEmpty(this.businesses) || _.isEmpty(this.disapprovedBusinessIds)){
          return disapproved;
        }
        for (const id in this.disapprovedBusinessIds){
          if (this.disapprovedBusinessIds[id] === false){
            let business = this.businesses[id];
            if (business){
              business.id = id;
              disapproved.push(business);
            }
          }
        }
        return disapproved;
      }
    },

    mounted(){
      firebase
        .database()
        .ref('/approved')
        .on('value', snapshot => {
          console.log(snapshot.val());
          this.disapprovedBusinessIds = snapshot.val();
        })
      firebase
        .database()
        .ref('/businesses')
        .on('value', snapshot => {
          this.businesses = snapshot.val();
        })
    }
  }
</script>

