<template>
  <div>
    <h2>Business Types</h2>
    <ul>
      <li v-for="type in types" class="my-1 hover-highlight">
        <div class="d-flex">
          <div>{{type}}</div>
          <b-btn
            @click="() => {deleteType(type)}"
            class="ml-auto"
            variant="danger"
          >Delete</b-btn>
        </div>
      </li>
    </ul>
    <b-form @submit.prevent @submit="onSubmit">
      <b-form-input v-model="type" placeholder="New Type"></b-form-input>
    </b-form>
  </div>
</template>

<script>
  import firebase from 'firebase';

  export default {
    name: "Types",

    data(){
      return {
        types: [],
        type: ''
      }
    },

    mounted(){
      firebase
        .database()
        .ref('types')
        .on('value', snapshot => {
          this.types = snapshot.val();
        })
    },

    methods: {
      onSubmit(){
        firebase
          .database()
          .ref('types')
          .push(this.type)
        this.type = '';
      },

      deleteType(type){
        let id = Object.keys(this.types).find(key => this.types[key] === type)
        firebase
          .database()
          .ref(`types/${id}`)
          .remove();
      }
    }
  }
</script>

<style scoped>
  .hover-highlight:hover{
    background-color: white;
  }
</style>
