<template>
  <div>
    <h2>{{business.name}}</h2>
    <b-form @submit.prevent class="d-flex justify-content-center my-3" @submit="onSubmit">
      <b-form-select :options="types" class="w-50" v-model="type"></b-form-select>
      <b-button type="submit" variant="success"><b-icon-check-circle></b-icon-check-circle></b-button>
    </b-form>
    <div class="img">
      <img :src="business.primaryImage" class="hero-img"/>
      <div class="download-button">
        <a download :href="business.primaryImage">Download Image</a>
        <b-button @click="() => {reUpload(business)}">Re-upload</b-button>
      </div>

    </div>
    <div v-if="showUpload">
      <b-form-file v-model="imageToUpload"></b-form-file>
      <b-btn @click="upload" variant="success">Upload Image</b-btn>
    </div>
    <div class="description mt-4">
      {{business.description}}
    </div>

    <h3 class="mt-5">Offers</h3>
    <div v-for="offer in business.offers">
      <div v-if="!offer.disabled" class="d-flex">
        <div>
          {{offer.description}}
        </div>
        <div class="ml-auto">
          Cooldown: {{offer.cooldown}}
        </div>
      </div>

    </div>

    <h3 class="mt-5">Approval</h3>
    <div v-if="approved">
      Approved
      <div>
        <b-button @click="disapprove" variant="danger" class="m-2">Disapprove</b-button>
      </div>
    </div>
    <div v-if="approved === false">
      Disapproved
      <div>
        <b-button @click="approve" variant="success" class="m-2">Approve</b-button>
      </div>
    </div>
    <div v-if="approved === null">
      Pending
      <div class="d-flex justify-content-center">
        <b-button @click="approve" variant="success" class="m-2">Approve</b-button>
        <b-button @click="disapprove" variant="danger" class="m-2">Disapprove</b-button>
      </div>
    </div>
  </div>
</template>

<script>
  import firebase from 'firebase';

  export default {
    name: "Business",
    data(){
      return {
        business: {},
        approved: null,
        types: [],
        type: '',
        showUpload: false,
        imageToUpload: null,
      }
    },

    mounted(){
      firebase
        .database()
        .ref(`/businesses/${this.$route.params.id}`)
        .on('value', snapshot => {
          this.business = snapshot.val();
          this.type = this.business.type
        })
      firebase
        .database()
        .ref(`/approved/${this.$route.params.id}`)
        .on('value', snapshot => {
          this.approved = snapshot.val();
        })
      firebase
        .database()
        .ref('types')
        .on('value', snapshot => {
          this.types = Object.values(snapshot.val());
        })
    },

    methods: {
      disapprove(){
        const id = this.$route.params.id;
        let updateObj = {}
        updateObj[id] = false;
        firebase
          .database()
          .ref(`/approved`)
          .update(updateObj)
      },

      approve(){
        const id = this.$route.params.id;
        let updateObj = {}
        updateObj[id] = true;
        firebase
          .database()
          .ref(`/approved`)
          .update(updateObj)
      },

      onSubmit(){
        firebase
          .database()
          .ref(`/businesses/${this.$route.params.id}`)
          .update({
            'type': this.type
          })
      },

      upload(){
        if (!this.imageToUpload){return;}
        firebase
          .storage()
          .ref()
          .child(`/businesses/primary/${this.business.owner}/${this.business.id}`)
          .put(this.imageToUpload)
      },

      reUpload: async(business) => {
        const response = await fetch(business.primaryImage);
        const blob = await response.blob();
        var ref = firebase.storage().ref().child(`/businesses/primary/${business.owner}/${business.id}`)
        ref.put(blob, {
          contentType: 'image/jpeg'
        });
      }
    }
  }
</script>

<style scoped>
  .hero-img{
    width: 50%;
    height: auto;
  }

  .download-button{
    position: absolute;
    top: 5px;
    right: 5px;
  }

  .img {
    position: relative;
  }
</style>
