<template>
  <div id="app">
    <Nav/>
    <div class="container mt-3">
      <router-view v-if="loggedIn"/>
      <Login v-else/>
    </div>
  </div>
</template>


<script>
  import firebase from 'firebase';
  import Login from "./views/Login";
  import Nav from "./components/Nav";

  export default {
    name: 'App',
    components: {Nav, Login},
    data(){
      return {
        loggedIn: false,
      }
    },

    mounted(){
      firebase.auth().onAuthStateChanged(user => {
        this.loggedIn = user;
      })
    }
  }
</script>

